<template>
   <div class="ContMovies">
      <div class="BxMovie" v-if="getFistData === 'NFM' || getFistData === undefined ">
         <div class="BxNotReady">
           Not Found Movie
         </div>
      </div>
      <div class="BxMovie" v-if="getFistData !== 'NFM' && getFistData !== undefined">
         <div 
            class="BxFrame" 
            v-for="(n, i) in getUpFistData" 
            :key="i"
            :class="{ BxPreload: getLoader }"
            @click="SelectedMovie(n.imdb)">
            <div class="BxFrame__Poster">
               <!-- <img src="../../../assets/image/cyAlYJUrE77k19MoH5pcMakh6YD.jpg" alt=""> -->
               <img v-if="n.poster" :src="n.poster" alt="poster">
               <img v-else src="../../../assets/image/opss.png" alt="poster">
            </div>
            <div class="BxFrame__InfoUp">
               <p class="BxFrame__InfoUp--quality">{{n.quality | NotEmpty}}</p>
               <div class="BxFrame__InfoUp--rating">
                   <i class="fa fa-star"></i>
                   <p>{{n.rating | NotEmpty}}</p>
               </div>
              
            </div>
            <div class="BxFrame__InfoBottom">
               <span class="BxFrame__InfoBottom--Titles">
                  <p>{{n.title | NotEmpty}}</p>
                  <p>( {{n.year | NotEmpty}} )</p>
               </span>
            </div>
         </div>

         <div class="BxFrame" 
            v-for="(s, i) in 5" :key="'A'+ i" 
            style="visibility: hidden;height:20px !important;margin-bottom:0 !important;margin-top:0 !important">
         </div>
      </div>


      <div class="BXpaginations" v-if="getFistData !== 'NFM' && getFistData !== undefined">
         <v-app>
            <v-pagination 
               color="#4dcba1" dark circle 
               v-model="currentPage" 
               :length="getLastPage"  
              >
            </v-pagination>

         </v-app>
      </div> 

   </div>
</template>

<script>
   import { AllFilter } from '../../Mixins/MixFilter'
   export default {
      mixins: [AllFilter],
      props:[
         'getLoader'
      ],
      data: () => ({ 
         currentPage: 1,
         maxItemPerPage: 19,
         lastPage: null
      }),
      computed: {
         //Catch data movie from State FirstData if data null will be outomate be 'NFD
         getFistData(){
            return this.$store.state.FirstData ? this.$store.state.FirstData.data :'NFD'
         },
         
         //This to calculate data from getFistData for pagination
         getUpFistData(){
            return this.getFistData.filter((item, index) => {
               return(
                  index < this.currentPage * this.maxItemPerPage && index >= (this.currentPage - 1) * this.maxItemPerPage
               )
            })
         },
         isMaxPage(){
            return this.currentPage >= this.getLastPage
         },

         //get value last page for pagination
         getLastPage(){
            const vals = Math.ceil(this.getFistData.length / this.maxItemPerPage)
            return this.lastPage = vals
         }
      },
      methods: {
         SelectedMovie(imdb){
            const IdMovie = imdb
            this.$store.commit('MutPlayerStat', true);
            this.$store.dispatch('GET_PLAYER_DATA', IdMovie);

            //To active loader to true
            this.$store.commit('MutLoaderDt', true);
         }
      },
   }
</script>


